.place_section_container {
  display: block;
}

.place_section_contents {
  padding: 4px 18px;

  .place_section {
    position: relative;
    padding: 10px 0;
    font-family: -apple-system, BlinkMacSystemFont, helvetica,
      "Apple SD Gothic Neo", sans-serif;

    &.border {
      border-top: 1px solid;
      border-top-color: #ecf0f2;
    }
  }
}
